.ReactModal__Overlay {
  z-index: 1;
}

.modal-responsive-freeze {
  position: absolute;
  background-color: #fff;
  padding: 20px;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  height: 511px;
  width: 652px;
  border-radius: 30px;
  box-shadow: 0px 0px 6px 0px lightgrey;
  border: 0;
  outline: transparent;
}

@media (max-width: 425px) {
  .modal-responsive-freeze {
    width: 310px;
  }
}

@media (max-width: 320px) {
  .modal-responsive-freeze {
    width: 274px;
  }
}

@media (max-width: 735px) {
  .modal-responsive-freeze {
    width: 464px;
    overflow-y: scroll;
  }
}

@media (max-width: 500px) {
  .modal-responsive-freeze {
    width: 358px;
  }
}

@media (max-width: 372px) {
  .modal-responsive-freeze {
    width: 278px;
    overflow-y: scroll;
  }
}
