.map {
  width: 100%;
  height: 100vh;
  position: relative;
}

.location-icon {
  font-size: 2rem;
  color: red;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loader img {
  width: 400px;
}

.loader h1 {
  margin-top: -100px;
}

.location-info {
  position: absolute;
  top: -100%;
  left: 50%;
  transform: translate(-50%, -100%);

  width: 500px;
  min-height: 200px;

  padding: 20px;

  color: #fff;
  font-size: 18px;

  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 3px 30px #0000005e;
}

.location-info:after {
  content: ' ';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -15px;

  border-top: 15px solid #fff;
  border-right: 15px solid transparent;
  border-left: 15px solid transparent;
  border-bottom: none;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
  color: #000;
}

.actionButtons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.noteBox {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;

  background-color: #ffffcc;
  border-top: 24px solid #f3f391;
  height: 110px;
  width: 95px;
  font-size: 10px;
  margin-top: -40px;
}
